import * as React from 'react';
import { FilterGroup, FilterDisplay } from 'oned-react';

const CHECKED_DATA = [
  {
    id: 'biz-area',
    options: [
      {
        value: 'a',
      },
    ],
  },
  {
    id: 'proj',
    options: [
      {
        value: 'a',
      },
    ],
  },
];

const DATA = [
  {
    id: 'biz-area',
    des: '业务版块空间',
    options: [
      {
        value: 'a',
        des: '国内板块空间',
        count: 12,
        checked: true,
      },
    ],
  },
  {
    id: 'sub-area',
    des: '业务子空间',
    options: [
      {
        value: 'a',
        des: '国内板块空间',
        count: 12,
      },
    ],
  },
  {
    id: 'proj',
    des: '所在项目',
    options: [
      {
        value: 'a',
        des: '淘系公共层',
        count: 12,
        checked: true,
      },
      {
        value: 'b',
        des: '淘系基础层',
        count: 12,
      },
    ],
  },
  {
    id: 'data-domain',
    des: '数据域',
    options: [
      {
        value: 'public',
        des: '公共域',
        count: 12,
      },
      {
        value: 'trade',
        des: '交易域',
        count: 12,
        checked: true,
      },
      {
        value: 'item',
        des: '商品域',
        count: 12,
      },
      {
        value: 'user',
        des: '会员域',
        count: 12,
      },
    ],
  },
];
class Demo1State {
  checkedData = CHECKED_DATA;
}

class Demo1 extends React.Component<{}, Demo1State> {
  state = {
    checkedData: CHECKED_DATA,
  };

  handleChange = ({ checkedData }) => {
    this.setState({
      checkedData,
    });
  };

  handleRemoveOption = ({ checkedData }) => {
    this.setState({
      checkedData,
    });
  };

  render() {
    return (
      <div>
        <div>
          已选中的值：
          <FilterDisplay data={DATA} checkedData={this.state.checkedData} onChange={this.handleRemoveOption} />
        </div>
        <FilterGroup data={DATA} checkedData={this.state.checkedData} onChange={this.handleChange} />
      </div>
    );
  }
}

export default <Demo1 />;

